/* mainPage 영역: 높이를 100vh로 고정해서 내부 스크롤이 발생하지 않도록 함 */
.mainPage {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  
  /* banner 영역: Topbar와 Footer를 제외한 나머지 영역을 중앙 정렬 */
  .banner {
    flex: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  /* 슬라이더 컨테이너: 화면 중앙에 위치하며 폭 50% */
  .slider-container {
    margin-top: 150px;
    width: 45%;
    overflow: hidden;
    position: relative;
  }
  
  /* slider: 내부의 슬라이드들을 가로로 나열 */
  .slider {
    display: flex;
  }
  
  /* 각 슬라이드: 너비는 JS에서 동적으로 설정 */
  .slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* 이미지: 슬라이드에 맞게 조정 */
  .slide img {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  /* 배너 텍스트: 단순하게 표시 */
  .banner-text {
    display: flex;
    margin-top: 4px;
    width: 45%;
    justify-content: space-between;
    font-size: 0.8rem;

  }

  /* 공통 페이지 스타일 */
  .page {
    padding: 100px 50px 50px;
    max-width: 1200px;
    margin: 0 auto;
  }

  .page .title {
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 50px;
    letter-spacing: 2px;
  }

  /* Publication 페이지 스타일 */
  .publications {
    max-width: 800px;
    margin: 0 auto;
  }

  .year {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 40px 0 20px;
    color: #333;
    border-bottom: 2px solid #eee;
    padding-bottom: 10px;
  }

  .bibliography {
    margin-bottom: 30px;
    padding: 20px;
    background: #fafafa;
    border-left: 4px solid #007bff;
  }

  .pub-title {
    font-size: 1.1rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 8px;
    line-height: 1.4;
  }

  .pub-authors {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 8px;
  }

  .pub-authors strong {
    color: #333;
  }

  .pub-others {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 0.85rem;
    color: #888;
  }

  .pub-venue {
    font-style: italic;
  }

  .pub-state {
    color: #28a745;
    font-weight: bold;
  }

  .pub-awards {
    color: #dc3545;
    font-weight: bold;
  }

  .pub-link a {
    color: #007bff;
    text-decoration: none;
  }

  .pub-link a:hover {
    text-decoration: underline;
  }

  /* 애니메이션 효과 */
  .animation {
    opacity: 1;
    transform: translateY(0);
    transition: all 0.6s ease;
  }

  .page > * {
    opacity: 0;
    transform: translateY(20px);
  }

  .page .animation {
    opacity: 1;
    transform: translateY(0);
  }

  /* 모바일 반응형 */
  @media (max-width: 768px) {
    .page {
      padding: 80px 20px 30px;
    }
    
    .page .title {
      font-size: 1.5rem;
    }
    
    .year {
      font-size: 1.2rem;
    }
    
    .bibliography {
      padding: 15px;
    }
    
    .pub-title {
      font-size: 1rem;
    }
    
    .pub-others {
      flex-direction: column;
      align-items: flex-start;
    }
    
    .pub-others > div {
      margin-bottom: 4px;
    }
  }